import { fetchGraphQl } from '@/personalization/api';
import { StoreConfigModel } from '@/personalization/data/models';
import { transformStoreConfig } from '@/personalization/data/transforms';
import { handleFetchError } from '@/personalization/lib/fetch-error';

import { STORE_CONFIG_QUERY } from './graphql/StoreConfigQuery';

export const getStoreConfig = async (): Promise<StoreConfigModel | null> => {
  const storeConfigString = sessionStorage.getItem('personalizationStoreConfig');
  if (storeConfigString !== null) {
    return JSON.parse(storeConfigString);
  }
  return fetchGraphQl(STORE_CONFIG_QUERY, {
    method: 'GET',
    cache: 'force-cache',
  }).then(({ errors, data }) => {
    if (errors) return handleFetchError(errors);

    const transformedStoreConfig = transformStoreConfig(data.storeConfig);
    sessionStorage.setItem('personalizationStoreConfig', JSON.stringify(transformedStoreConfig))
    return transformedStoreConfig;
  });
};
