import { StoreConfigModel } from '@/personalization/data/models';

export function transformStoreConfig(storeConfig: any): StoreConfigModel | null {
  if (!storeConfig) return null;

  return {
    shareActiveSegments: storeConfig.share_active_segments,
    shareCustomerGroup: storeConfig.graphql_share_customer_group,
    shareAppliedCartRule: storeConfig.share_applied_cart_rule,
    customerAccessTokenLifetime: storeConfig.customer_access_token_lifetime,
  };
}
