/* eslint-disable no-useless-escape */
import { getStoreConfig } from '@/personalization/api';

export const COOKIE_LIFETIME_HOURS = 1;
export const COOKIE_NAME = 'personalization_dropin_data';

export const setCookie = async (value: string) => {
  const lifetime = await getCookiesLifetime();
  document.cookie = `${COOKIE_NAME}=${encodeURIComponent(value)}; path=/; ${lifetime}; `;
}

export const getCookie = () => {
  const cookies = document.cookie.split(';');
  let foundValue;

  cookies.forEach((cookie) => {
    const [name, value] = cookie.trim().split('=');
    if (name === COOKIE_NAME) {
      foundValue = decodeURIComponent(value);
    }
  });

  return foundValue;
};

export const deleteCookie = () => {
  document.cookie = `${COOKIE_NAME}=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;`;
};

const getCookiesLifetime = async () => {
  try {
    const storeConfig = await getStoreConfig();
    const accessTokenLifeTime = (storeConfig?.customerAccessTokenLifetime || COOKIE_LIFETIME_HOURS) * 3600;
    return `Max-Age=${accessTokenLifeTime}`;
  } catch (error) {
    console.error('getCookiesLifetime() Error:', error);
    return `Max-Age=${COOKIE_LIFETIME_HOURS * 3600}`;
  }
};
