const visibleTypes: string[] = [];

export const registerTypeIsFirst = (type:  string | undefined): boolean => {
  if (type === undefined) {
    return true;
  }
  if (visibleTypes.includes(type)) {
    return false;
  }
  visibleTypes.push(type);
  return true;
}

export const resetTypes = (): void => { visibleTypes.length = 0; };