import { PersonalizationData } from '@/personalization/data/models';
import { getCookie } from "@/personalization/lib/cookieUtils";

export const getPersonalizationData = (): PersonalizationData =>  {
  const data = getCookie();
  if (data === undefined) {
    return {
      segments: [],
      groups: [],
      cartRules: [],
    };
  }
  const decodedData = JSON.parse(data);
  return decodedData ? decodedData : {
    segments: [],
    groups: [],
    cartRules: [],
  };
}
